import { useState } from 'react'
import Radio from './Radio'
import RadioGroup from './RadioGroup'

function CountryMaker(props) {
  const onChange = props.onChange
  const country = props.value
  const list = props.isLocal
    ? props.isBeef
      ? [' ', ' ']
      : ['', ' ']
    : ['', '', '', '', '', '']

  const countryList = list.map((country, index) => (
    <Radio key={index} value={country} name='countries'>
      {country}
      <br />
    </Radio>
  ))
  return (
    <RadioGroup label='' value={country} onChange={onChange}>
      <>{countryList}</>
    </RadioGroup>
  )
}

export default CountryMaker
