import { useEffect, useState } from 'react'
import Radio from './Radio'
import RadioGroup from './RadioGroup'

function GradeMaker(props) {
  const [list, setList] = useState([])
  const onChange = props.onChange
  const grade = props.value
  useEffect(() => {
    if (props.isBeef) {
      switch (props.whichCountry) {
        case ' ':
        case ' ': {
          setList(['1++', '1+', '1', '2', '3', '()'])
          break
        }
        case '': {
          setList(['', '', '', ''])
          break
        }
        case '': {
          setList(['MB 7/8/9', 'MB 4/5/6', 'MB 1/2/3', 'GF', 'S', 'A'])
          break
        }
        case '': {
          setList(['', 'AAA', 'AA', 'A', '()'])
          break
        }
        case '': {
          setList(['()'])
          break
        }
        case '': {
          setList(['', ''])
          break
        }
        case '': {
          setList(['', '', ''])
          break
        }
        default: {
          setList([])
        }
      }
    } else {
      switch (props.whichCountry) {
        case '':
        case ' ': {
          setList(['1+', '1', '2', '()'])
          break
        }
        case '':
        case '':
        case '':
        case '':
        case '':
        case '': {
          setList(['()'])
          break
        }
        default: {
          setList([])
        }
      }
    }
  }, [props.whichCountry, props.isBeef])

  const gradeList = list.map((grade, index) => (
    <Radio key={index} value={grade} name='grades'>
      {grade}
      <br />
    </Radio>
  ))

  return (
    <RadioGroup label='' value={grade} onChange={onChange}>
      <>{gradeList}</>
    </RadioGroup>
  )
}

export default GradeMaker
